export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview height=\"200px\">\n      <VAsyncContent\n        :loading=\"loading\"\n        :error=\"error\"\n        :reloadable=\"reloadable\"\n        :spinner-size=\"spinnerSize\"\n        :loading-text=\"loadingText\"\n        :error-text=\"errorText\"\n        :reload-text=\"reloadText\"\n      >\n        Some content here...\n      </VAsyncContent>\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"loading\"\n          label=\"Loading\"\n        />\n        <KnobString\n          v-model=\"loadingText\"\n          label=\"Loading text\"\n        />\n        <KnobListItem\n          v-model=\"spinnerSize\"\n          :items=\"iconSizes\"\n          label=\"Spinner size\"\n        />\n        <KnobBoolean\n          v-model=\"error\"\n          label=\"Error\"\n        />\n        <KnobString\n          v-model=\"errorText\"\n          label=\"Error text\"\n        />\n        <KnobBoolean\n          v-model=\"reloadable\"\n          label=\"Reloadabilty\"\n        />\n        <KnobString\n          v-model=\"reloadText\"\n          label=\"Reload button text\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean,\n  KnobString\n} from '@/docs/components';\nimport {\n  VAsyncContent\n} from '@/components';\nimport { iconSizes } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'AsyncContentExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    KnobString,\n    VAsyncContent\n  },\n  data () {\n    return {\n      iconSizes,\n      loading: true,\n      error: false,\n      reloadable: false,\n      spinnerSize: '2x',\n      loadingText: 'loading text',\n      errorText: 'error text',\n      reloadText: 'reload text'\n    };\n  },\n  mounted () {\n    setTimeout(() => {\n      this.loading = false;\n    }, 2000);\n  }\n};\n</script>\n";